import React from 'react'
import { injectGlobal } from 'emotion'

injectGlobal`
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Source Sans Pro', sans-serif;
  }
  body {
    background: pink;
  }
`

const Layout = ({ children }) => (
  <div>
    <main>{children()}</main>
  </div>
)

export default Layout



